        </c-col>\n            </v-row>\n            <v-row>\n                <c-col md=\"6\" sm=\"6\">\n                    <v-text-field\n                        v-model=\"formData.number\"\n                        label=\"No.\"\n                        required\n                        outlined\n                        dense\n                        clearable\n                        :disabled=\"!$store.getters.isAdmin\"\n                    ></v-text-field>\n                </c-col>\n            </v-row>\n            <v-row>\n                <c-col md=\"6\" sm=\"6\">\n                    <v-menu\n                        v-model=\"incorporationDateMenu\"\n                        :close-on-content-click=\"false\"\n                        :nudge-right=\"40\"\n                        transition=\"scale-transition\"\n                        offset-y\n                        min-width=\"auto\"\n                    >\n                        <template v-slot:activator=\"{ on, attrs }\">\n                            <v-text-field\n                                v-model=\"formData.incorporation_date\"\n                                label=\"Incorporation Date\"\n                                outlined\n                                dense\n                                clearable\n                                readonly\n                                v-bind=\"attrs\"\n                                v-on=\"on\"\n                                :disabled=\"!$store.getters.isAdmin\"\n                            ></v-text-field>\n                        </template>\n                        <v-date-picker\n                            v-model=\"formData.incorporation_date\"\n                            @input=\"incorporationDateMenu = false\"\n                        ></v-date-picker>\n                    </v-menu>\n                </c-col>\n            </v-row>\n            <v-text-field\n                v-model=\"formData.address\"\n                label=\"Address\"\n                required\n                outlined\n                dense\n                clearable\n                :disabled=\"!$store.getters.isAdmin\"\n            ></v-text-field>\n            <v-text-field\n                v-model=\"formData.registered_share_capital\"\n                label=\"Registered Share Capital\"\n                required\n                outlined\n                dense\n                clearable\n                :disabled=\"!$store.getters.isAdmin\"\n            ></v-text-field>\n            <v-text-field\n                v-model=\"formData.share_holders\"\n                label=\"Share Holders\"\n                hint=\"Separate keywords with an English comma\"\n                required\n                outlined\n                dense\n                clearable\n                :disabled=\"!$store.getters.isAdmin\"\n            >\n            </v-text-field>\n            <v-text-field\n                v-model=\"formData.directors\"\n                label=\"Directors\"\n                hint=\"Separate keywords with an English comma\"\n                required\n                outlined\n                dense\n                clearable\n                :disabled=\"!$store.getters.isAdmin\"\n            >\n            </v-text-field>\n            <hr />\n            <CButton\n                @click=\"update\"\n                v-if=\"$store.getters.isAdmin\"\n                color=\"primary\"\n                class=\"px-4\"\n            >\n                Update\n                <v-progress-circular\n                    v-if=\"loading\"\n                    indeterminate\n                    color=\"primary\"\n                    :size=\"15\"\n                ></v-progress-circular>\n            </CButton>\n        </form>\n    </div>\n</template>\n<script>\n//\nexport default {\n    name: \"CompanyForm\",\n    components: {},\n    props: {\n        id: null\n    },\n    data() {\n        return {\n            formData: {},\n            users: {\n                items: [],\n                loading: false\n            },\n            incorporationDateMenu: false,\n            loading: false\n        };\n    },\n    watch: {\n        users: [\n            function search(val) {\n                let self = this;\n                if (self.users.length > 0 || self.users.loading) return;\n                self.users.loading = true;\n                this.$store\n                    .dispatch(\"user/get\", {})\n                    .then(response => {\n                        self.users.items = response.data;\n                        self.users.loading = false;\n                    })\n                    .catch(error => {\n                        self.loading = false;\n                    });\n            }\n        ]\n    },\n    mounted() {\n        this.fetch();\n    },\n    methods: {\n        fetch() {\n            let self = this;\n            let data = {\n                id: self.$props.id\n            };\n            this.$store\n                .dispatch(\"company/details\", data)\n                .then(response => {\n                    self.formData = response.data.data;\n                })\n                .catch(error => {});\n        },\n        update() {\n            let self = this;\n            self.loading = true;\n            this.$store\n                .dispatch(\"company/update\", self.formData)\n                .then(response => {\n                    self.formData = response.data.data;\n                    self.loading = false;\n                })\n                .catch(error => {\n                    self.loading = false;\n                });\n        }\n    }\n};\n</script>\n\n<style scoped>\n.v-text-field >>> input {\n    font-size: 0.8em;\n    font-weight: 100;\n}\n.v-text-field >>> label {\n    font-size: 0.8em;\n}\n.v-text-field >>> button {\n    font-size: 0.8em;\n}\n</style>\n"],"sourceRoot":""}]);
